<template>
  <div class="wapper-box">
    <img
      src="@/assets/images/login-bg.jpg"
      alt=""
      srcset=""
      class="login-bg"
    />
    <div class="login-box">
      <div class="login-form-box">
        <el-form
          :model="userInfo"
          :rules="rules"
          class="login-form"
          ref="ruleForm"
        >
          <el-form-item prop="user">
            <el-input
              v-model="userInfo.user"
              class="input-call"
              placeholder="请输入用户名"
            >
              <img
                src="@/assets/images/icon-login-user.png"
                alt=""
                srcset=""
                slot="prefix"
                class="input-prefix"
              />
            </el-input>
          </el-form-item>
          <el-form-item prop="code">
            <el-input
              v-model="userInfo.code"
              class="input-call"
              placeholder="请输入验证码"
            >
               <span slot="append" class="code-btn" @click="getVerificeHandle">{{verificeStr}}</span>
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              :type="inputType"
              v-model="userInfo.password"
              class="input-call"
              placeholder="请输入密码"
            >
              <img
                src="@/assets/images/icon-luck.png"
                alt=""
                srcset=""
                slot="prefix"
                class="input-prefix"
              />
            </el-input>
          </el-form-item>
          <!-- <el-form-item>
            <span class="forget" @click="dialogVisible = true">忘记密码?</span>
          </el-form-item> -->

          <el-form-item>
            <el-button type="primary" class="btn" @click="loginHandle" 
              >登录</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
//   import { loginRequired } from "@/api/login"
//   import { md5 } from '@/utils/md5'
export default {
  data() {
    return {
      userInfo: {
        user: "",
        password: "",
      },
      inputType: "password",
      dialogVisible: false,
      verificeStr: "获取验证码",
      verificeTime: 59,
      verificeLoading: false,
      rules: {
        // user: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        // password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    loginHandle() {
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          //   loginRequired({user: this.userInfo.user, password: this.userInfo.password}).then((res) => {
          //     if (res.result === 0) {
          //     } else {
          //       this.$message({
          //         type:'warning',
          //         message: '账号或密码错误，请重试'
          //       });
          //     }
          //   });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    confirmHandle() {
      this.$router.push("/register");
    },
    getVerificeHandle() {
        if(this.verificeLoading) return
        this.getVerifice()
    },
    getVerifice() {
        this.verificeLoading = true
        this.verificeStr = this.verificeTime + "s";
        this.verificeTime--;
        if (this.verificeTime === 0) {
          this.verificeStr = "获取验证码";
          this.verificeTime = 59;
          this.verificeLoading = false;
          return;
        }
        setTimeout(() => {
          this.getVerifice();
        }, 1000);
    }
  },
};
</script>
<style scoped>
.wapper-box {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.login-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.logo {
  width: 337px;
  height: 83px;
  margin-bottom: 24px;
}
.login-box {
  width: 384px;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -60%);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login-form-box {
  width: 350px;
  background: #d2e2ec;
  border-radius: 8px;
  padding: 48px 48px 24px 48px;
  box-sizing: border-box;
}
.input-prefix {
  width: 16px;
  height: 16px;
  margin-top: 12px;
  margin-left: 2px;
}
.btn {
  width: 100%;
  height: 32px;
  line-height: 32px;
  padding: 0;
}
.forget {
  color: #0d70f2;
  cursor: pointer;
}
.code-btn {
    color: #0d70f2;
    cursor: pointer;
    font-size: 12px;
    display: inline-block;
    
  text-align: center;
    width: 60px;
}
</style>
<style>
.el-input__inner {
  height: 32px;
  font-size: 12px;
}
.el-form-item {
    margin-bottom: 12px;
}
</style>